.menu--open {
    display: none;
}

.side-menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 300px;
    height: 100%;
    background-color: white;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.3);
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    z-index: 100000;
    padding: 20px;
}

.side-menu--open {
    transform: translateX(0);
}

.side-menu .close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
}

.side-menu--nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

.side-menu h2 {
    margin: 20px 0;
}

.side-menu--icons {
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.side-menu--icon {
    height: 24px;
    width: 24px;
    object-fit: contain;
}

.side-menu ul {
    list-style: none;
    text-align: left;
    padding: 0;
}

.side-menu ul li {
    margin: 10px 0;
}

.side-menu ul li a {
    text-decoration: none;
    color: #333;
}