.login-container {
    max-width: 360px;
    /* 화면 크기에 따라 조정 */
    margin: 0 auto;
    padding-top: 40px;
    padding-bottom: 80px;
    text-align: left;
}

/* 제목 */
.login-container h2 {
    color: #333;
    margin-bottom: 1rem;
    font-weight: 500;
}

/* 인풋 박스 */
.input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}

.input-group input {
    padding: 0.6rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    color: #333;
    outline: none;
}

.input-group input:focus {
    border-color: #333;
}

/* 로그인 버튼 */
.login-button {
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.login-button:hover {
    background-color: #555;
}