/* 푸터 */
.footer {
    display: flex;
    flex-wrap: wrap;
    /* 화면이 좁아지면 줄바꿈 */
    background-color: #232323;
    color: #fff;
    padding-top: 40px;
    padding-bottom: 40px;
    /* 기본 패딩 */
    justify-content: space-between;
    /* 섹션 간 간격 균등 */
    gap: 20px;
    /* 세로 간격 */
    box-sizing: border-box;
    /* 패딩 포함한 박스 크기 */
}

/* 푸터 섹션 */
.footer-section {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    max-width: 200px;
}

.footer-logo {
    width: 50px;
    margin-bottom: 10px;
}

.footer-section p {
    text-align: left;
    margin-bottom: 10px;
}

.footer-section h4 {
    font-size: 16px;
    margin-top: 0px;
    margin-bottom: 30px;
}

.footer-section ul {
    list-style: none;
    padding: 0;
    margin-top: 0;
    text-align: left;
    font-size: 14px;
}

.footer-section ul li {
    margin-bottom: 15px;
    opacity: 0.6;
}

.footer-section.form-section {
    max-width: 267px;
    /* 폼 섹션은 기본 화면에서 267px로 고정 */
    width: 100%;
    /* 반응형을 위해 100% 너비 */
}

/* 폼 스타일 */
.footer-section form {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    padding: 0 16px;
    width: 100%;
    height: 46px;
    box-sizing: border-box;
}

/* 인풋 필드 */
.footer-section input {
    border: none;
    outline: none;
    background: transparent;
    color: white;
    font-size: 13px;
    flex: 1;
}

/* 버튼 */
.footer-section button {
    position: absolute;
    right: 2px;
    background: none;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 46px;
    height: 46px;
}

.footer-section button img {
    width: 23px;
    height: 15px;
    object-fit: contain;
    opacity: 0.2;
}