/* AboutContent.module.css */

/* Intro Section */
.aboutContent {
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 60px;
    box-sizing: border-box;
    text-align: center;
}

.introContent {
    white-space: pre-line;
    max-width: 800px;
    margin: 0 auto;
}

.introContent h2 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: #333;
}

.introContent p {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #666;
}