.shop {
    display: flex;
}

/* 좌측 필터 영역 */
.shop__left {
    flex: 0 0 200px;
    background: #fff;
    padding: 20px;
    text-align: left;
    margin-top: 20px;
    margin-bottom: 20px;
}

.shop__left-category {
    margin-bottom: 40px;
}

.shop__left-category h2 {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
}

.shop__left-subCategory div {
    font-size: 14px;
    margin-bottom: 5px;
}

/* 우측 컨텐츠 영역 */
.shop__right {
    flex: 1;
    padding: 20px;
    padding-right: 0;
    display: flex;
    flex-direction: column;
}

/* 상단 Sort by 영역 */
.shop__right-top {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 20px;
    position: relative;
}

.shop__sort {
    font-size: 14px;
    margin-left: 5px;
    padding: 8px 12px;
    border-radius: 4px;
    background-color: transparent;
    border: none;
    color: #333;
    cursor: pointer;
    /* appearance: none; */
}

.shop__sort:focus {
    border-color: #888;
    outline: none;
}

.shop__sort option {
    font-size: 14px;
    color: #333;
}

/* 메인 상품 카드 영역 */
.shop__right-main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
}

.pagination {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    gap: 10px;
}

.pagination__button {
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 5px 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.pagination__button:hover {
    background-color: #e0e0e0;
}

.pagination__button.active {
    background-color: #333;
    color: white;
    border-color: #333;
}

/* 반응형 대응 */
@media (max-width: 1024px) {
    .shop__right-main {
        grid-template-columns: repeat(2, 1fr);
    }

    .shop__left {
        flex: 0 0 180px;
    }
}

@media (max-width: 768px) {
    .shop {
        flex-direction: column;
    }

    .shop__left {
        flex: none;
        width: 100%;
        border-right: none;
        border-bottom: 1px solid #ddd;
    }

    .shop__right-main {
        grid-template-columns: 1fr;
    }
}