.imageSection {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 20px;
}

.imageBox {
    position: relative;
    height: 400px;
    width: 300px;
    background-color: #e0e0e0;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin-right: 40px;
}

.imageBox img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.placeholder {
    font-size: 14px;
    color: #aaa;
}

.fileInput {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
}

/* X 버튼 */
.removeButton {
    position: absolute;
    z-index: 2;
    top: 8px;
    right: 8px;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    cursor: pointer;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.removeButton:hover {
    background-color: rgba(255, 0, 0, 0.8);
}

.instructions {
    flex: 1;
}

.instructions h3 {
    margin-top: 5px;
    font-size: 18px;
    margin-bottom: 10px;
}

.instructions ul {
    margin: 0;
    padding-left: 14px;
    font-size: 14px;
    color: #555;
}