/* cart.css */

.cart-table {
    width: 100%;
    text-align: center;
    padding: 16px 0;
}

.cart-table__head-row {
    background: #DFE3E6;;
    height: 40px;
}

.cart-table__head-cell {
    border-bottom: 2px solid #ccc;
    color: #333;
}

.cart-table__body-row {
    border-bottom: 1px solid #ddd;
}

.cart-table__body-cell {
    vertical-align: middle;
    border-bottom: 1px solid #ddd;
    height: 80px;
}

.cart-table__image {
  max-height: 50px;
  height: auto;
  cursor: pointer;
}

.cart-table__body-cell--title p{
    font-size: 17px;
    font-weight: 500;
    cursor: pointer;
}

.cart-table__quantity-input {
  width: 60px;
  text-align: center;
  background-color: transparent;
  border: transparent;
}

.cart-table__remove-button {
  background: #333;
  color: #fff;
  border: none;
  cursor: pointer;
}

.cart-table__remove-button:hover {
  background: #e64848;
}

.cart-footer {
    display: flex;
    justify-content: space-between;
    /* 합계와 버튼을 양쪽으로 배치 */
    align-items: center;
    padding: 16px;
}

.cart-total {
    font-size: 16px;
    font-weight: bold;
}
.order-button {
    width: auto;
    height: 40px;
    background-color: #434242;
    color: white;
    vertical-align: middle;
    border: none;
    padding: 8px 16px;
    font-size: 14px;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease;
}

.order-button:hover {
    background-color: #545353;
}

.order-button:active {
    background-color: #7aaab6;
}

.order-button--disabled {
    background-color: #c5c5c5;
    color: white;
    pointer-events: none;
    cursor: default;
}