.descriptionSection {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.descriptionInput {
    width: 100%;
    height: 40px;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    box-sizing: border-box;
    font-size: 14px;
}

.descriptionTextarea {
    width: 100%;
    height: 100px;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    box-sizing: border-box;
    font-size: 14px;
    resize: vertical;
}