.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    /* 반투명 배경 */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal {
    min-width: 400px;
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.modal p {
    margin-bottom: 20px;
    font-size: 16px;
    color: #333;
}

.modal-buttons {
    display: flex;
    justify-content: right;
    gap: 10px;
}

.confirm-button,
.cancel-button {
    background-color: #434242;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
}

.confirm-button:hover,
.cancel-button:hover {
    background-color: #545353;
}