/* Card 내부 스타일 가정(Card 컴포넌트 내 클래스명 예: .card, .card-title, .card-price, .card-updated) */
.shop__card {
    background: white;
    overflow: hidden;
    padding: 10px 24px;
    cursor: pointer;
}

.shop__card-image {
    max-height: 330px;
    margin: 0 0 5px 0;
}

.shop__card-title {
    text-align: left;
    font-size: 16px;
    font-weight: 500;
    margin: 0 0 10px 0;
}

.shop__card-price {
    text-align: left;
    font-size: 15px;
    font-weight: 500;
    margin: 0 0 5px 0;
}

.shop__card-updated {
    text-align: left;
    font-size: 12px;
    color: #333333;
    margin: 0 0 5px 0;
}