/* Hero Section */
.aboutHero {
    box-sizing: border-box;
    padding: 60px 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: #f8f9fa;
}

.aboutHeroBox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

/* 좌측 로고 */
.aboutHeroLeft {
    flex: 1.2;
    text-align: center;
}

.logo {
    width: 60%;
    max-width: 400px;
    object-fit: contain;
}

/* 우측 텍스트 */
.aboutHeroRight {
    flex: 0.8;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
}

/* 반응형 폰트 크기 */
.title {
    font-size: clamp(2rem, 5vw, 3.5rem);
    /* 2rem ~ 3.5rem */
    color: #333;
    margin: 0;
    transform: translateY(20px);
    opacity: 0;
    transition: all 0.8s ease-out;
}

.subtitle {
    font-size: clamp(1rem, 2.5vw, 1.5rem);
    /* 1rem ~ 1.5rem */
    color: #666;
    margin: 0;
    transform: translateY(20px);
    opacity: 0;
    transition: all 0.8s ease-out;
}

/* 나타나는 애니메이션 */
.aboutVisible {
    transform: translateY(0);
    opacity: 1;
}

/* 화살표 이미지 */
.aboutHeroRight .imgBox {
    width: 100%;
}

.aboutHeroRight img {
    position: relative;
    left: 20%;
    margin-top: 5%;
    animation: floatUpDown 6s ease-in-out infinite;
}

@keyframes floatUpDown {
    20% {
        transform: translateY(0px);
    }

    40% {
        transform: translateY(20px);
    }

    60% {
        transform: translateY(0px);
    }

    80% {
        transform: translateY(20px);
    }

    100% {
        transform: translateY(0px);
    }
}

/* 반응형 디자인 */
@media (max-width: 1024px) {
    .aboutHeroBox {
        height: 80%;
        flex-direction: column;
        justify-content: space-between;
        text-align: center;
    }

    .aboutHeroLeft,
    .aboutHeroRight {
        text-align: center;
    }

    .logo {
        width: 65%;
    }

    .aboutHeroRight img {
        left: 0;
    }
}

@media (max-width: 600px) {
    .logo {
        width: 70%;
    }

    .aboutHero {
        padding: 40px 5%;
    }

    .aboutHeroRight {
        gap: 10px;
    }

    .title {
        font-size: clamp(1.5rem, 6vw, 2.5rem);
    }

    .subtitle {
        font-size: clamp(0.9rem, 4vw, 1.2rem);
    }
}