.sellPage {
    padding-top: 20px;
    padding-bottom: 20px;
    margin: 0 auto;
    text-align: left;
}

.submitButton {
    width: 100%;
    margin-top: 20px;
    padding: 10px;
    background-color: #DFE3E6;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* 활성화된 버튼의 hover 효과 */
.submitButton:hover {
    background-color: #f5b536;
}

/* 비활성화된 버튼 스타일 */
.submitButton:disabled {
    background-color: #ccc;
    cursor: not-allowed;
    opacity: 0.6;
}

/* 비활성화된 버튼에 hover 효과 무시 */
.submitButton:disabled:hover {
    background-color: #ccc;
}