.mypage-container {
    max-width: 500px;
    margin: 0 auto;
    padding-top: 80px;
    padding-bottom: 80px;
    color: #333;
}

.mypage-container h2 {
    margin-bottom: 1.5rem;
    font-weight: 500;
}

/* 프로필 섹션 */
.profile-section {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
    text-align: left;
}

.profile-image {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 24px;
}

.profile-info {
    display: flex;
    flex-direction: column;
}

.profile-name {
    font-size: 1.2rem;
    font-weight: 600;
}

.profile-email {
    font-size: 0.9rem;
    color: #666;
    margin-top: 0.2rem;
}

/* 버튼 섹션 */
.button-section {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.sell-button {
    display: block;
    background-color: #333;
}

.button-wrapper {
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
}

.mypage-container button {
    flex: 1;
    padding: 0.8rem;
    border: none;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 4px;
    color: #fff;
    transition: background-color 0.2s ease;
    background-color: #333;
}

.mypage-container button:hover {
    background-color: #555;
}
