/* Event.css - 개선된 버전 */

/* 전체 카드 스타일 */
.event-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 40px auto;
  padding: 40px;
  background-color: #ffffff;
  gap: 20px;
}

/* 이미지 영역 */
.event-image {
  flex: 1 1 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.event-image img {
  width: 100%;
  max-width: 400px;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.event-image img:hover {
  transform: scale(1.05);
}

/* 상세 정보 영역 */
.event-details {
  flex: 1 1 600px;
  padding: 20px;
  color: #333;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.event-details h1 {
  font-size: 2.8rem;
  margin-bottom: 15px;
  color: #222;
}

.event-details p {
  font-size: 1.2rem;
  line-height: 1.5;
  margin-bottom: 15px;
}

/* 리스트 스타일 (기존에 이모지가 있으므로 별도 마커는 제거) */
.event-details ul {
  list-style: none;
  padding-left: 0;
  margin-bottom: 20px;
}

.event-details ul li {
  font-size: 1.2rem;
  margin-bottom: 8px;
  /* 이모지가 이미 들어있으므로 별도의 마커는 사용하지 않음 */
}

/* 신청하기 버튼 스타일 */
.apply-button {
  display: inline-block;
  font-size: 1.4rem;
  padding: 15px 30px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  margin-top: 20px;
  text-decoration: none;
}

.apply-button:hover {
  background-color: #0056b3;
  transform: translateY(-3px);
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .event-container {
    flex-direction: column;
    padding: 20px;
  }
  .event-details {
    padding: 10px;
    text-align: center;
  }
  .event-details h1 {
    font-size: 2.4rem;
  }
  .event-details p,
  .event-details ul li {
    font-size: 1.1rem;
  }
  .apply-button {
    font-size: 1.3rem;
    padding: 12px 25px;
  }
}
