/* index.css */

/* 전역 초기화 및 폰트 설정 */
body {
  margin: 0;
  margin-top: 65px;
  /* 네비게이션 등 상단 여백 */
  font-family: 'Roboto', sans-serif;
  background-color: #f7f7f7;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* 코드 태그용 폰트 */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* App 레이아웃 */
.App {
  text-align: center;
}

/* 공통 패딩 */
.common-padding {
  padding-left: 80px;
  padding-right: 80px;
}

.navbarOffsetHeight {
  height: calc(100vh - 65px);
}

/* 반응형 (Responsive) 패딩 조정 */
@media (max-width: 1320px) {
  .common-padding {
    padding-left: 60px;
    padding-right: 60px;
  }
}

@media (max-width: 1000px) {
  .common-padding {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media (max-width: 650px) {
  .common-padding {
    padding-left: 20px;
    padding-right: 20px;
  }
}