/* 전체 섹션 스타일 */
.detail {
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 5%;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* 왼쪽 섹션 스타일 */
.detail__left {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.breadcrumb-wrapper {
    width: 100%;
    padding-left: 15px;
}

.breadcrumb {
    display: flex;
    list-style: none;
    padding: 0;
    margin-bottom: 30px;
    font-size: 14px;
    color: #333;
    text-align: left;
}

.breadcrumb-item {
    display: flex;
    align-items: center;
}

.breadcrumb-item a {
    text-decoration: none;
    color: #555;
}

.breadcrumb-item a:hover {
    text-decoration: underline;
}

.breadcrumb-item:not(:last-child)::after {
    content: "/";
    margin: 0 8px;
    color: #999;
}

.breadcrumb-item span {
    color: #000;
    font-weight: bold;
}

.detail__image {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
}

/* 오른쪽 섹션 스타일 */
.detail__right {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding: 20px;
    background-color: transparent;
    border-radius: 10px;
    text-align: left;
}

.detail__right button {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #f9f9f9;
    background-color: #434242;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.detail__right button:hover {
    background-color: #545353;
}

/* 반응형 디자인 */
@media (max-width: 768px) {
    .detail {
        flex-direction: column;
        align-items: center;
        height: auto;
    }

    .detail__right {
        width: 100%;
        margin-top: 20px;
    }
}