/* Process Section */
.about-process {
  text-align: center;
  margin-bottom: 60px;
}

.about-process h2 {
  font-size: 2rem;
  margin-bottom: 40px;
  color: #333;
}

.process-steps {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.step {
  padding: 20px;
}

.step-number {
  width: 40px;
  height: 40px;
  background-color: #333;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 20px;
  font-size: 1.2rem;
}

.step h4 {
  font-size: 1.3rem;
  margin-bottom: 15px;
  color: #333;
}

.step p {
  color: #666;
}

/* Contact Section */
.about-contact {
  text-align: center;
  padding: 40px;
  background-color: #f8f9fa;
  border-radius: 10px;
}

.about-contact h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
}

.contact-info {
  margin-top: 30px;
}

.contact-info p {
  margin: 10px 0;
  color: #666;
}

/* Responsive Design */
@media (max-width: 768px) {
  .about-features,
  .process-steps {
    grid-template-columns: 1fr;
  }

  .about-hero h1 {
    font-size: 2.5rem;
  }

  .about-hero .subtitle {
    font-size: 1.2rem;
  }

  .feature-card {
    padding: 20px;
  }
}
