.navbar {
    z-index: 20000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(247, 247, 247, 0.5);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    color: #121212;
    padding-top: 10px;
    padding-bottom: 10px;
    box-sizing: border-box;
}

.navbar-content {
    height: 45px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.logo img {
    height: 45px;
}

.navbar-categories {
    display: flex;
    justify-content: space-between;
    width: 583px;
    text-align: center;
}

.category-item {
    position: relative;
    cursor: pointer;
    height: 65px;
    display: flex;
    align-items: center;
    text-align: center;
}

.category-link {
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    font-weight: 500;
    color: #121212;
    text-align: center;
    text-decoration: none;
}

.category-item::after {
    content: '';
    position: absolute;
    bottom: 15px;
    left: -20%;
    width: 0;
    height: 2px;
    background-color: #121212;
    transition: width 0.3s ease;
}

.category-item:hover::after {
    width: 140%;
}

.dropdown {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 800;
    background-color: #f7f7f7;
    color: #121212;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 0 40px;
    padding-top: 65px;
    text-align: center;
    border-top: 1px solid #444;
    display: flex;
    justify-content: center;
    height: 65px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}

.dropdown-wrapper {
    padding: 0;
    width: 80%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f7f7f7;

    transform: translateY(-65px);
    transition: transform 0.45s ease;
}

.dropdown.visible {
    opacity: 1;
    visibility: visible;
    height: 80px;
}

.dropdown.visible .dropdown-wrapper {
    transform: translateY(0);
}

.dropdown-item {
    padding: 10px 0;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 500px;
    color: #121212;
    text-decoration: none;
    cursor: pointer;
    transition: background-color 1.0s ease;
}

.dropdown-item:hover {
    color: #3444;
}

.navbar-icons {
    display: flex;
    flex-direction: row;
    gap: 15px;
    position: relative;
}

.search-bar {
    position: absolute;
    top: -10%;
    right: 63px;
    width: 0;
    height: 120%;
    background-color: #f7f7f7;
    border-radius: 20px;
    overflow: hidden;
    display: flex;
    align-items: center;
    padding: 0;
    transition: all 0.3s ease-in-out;
    opacity: 0;
    visibility: hidden;
}

.search-bar--open {
    width: 280px;
    padding: 0 10px;
    opacity: 1;
    visibility: visible;
}

.search-bar input {
    flex: 1;
    border: none;
    border-bottom: 1px solid #121212;
    outline: none;
    font-size: 12px;
    padding-left: 10px;
    height: 27px;
    background-color: transparent;
}

.search-bar .search-btn {
    background: none;
    border: none;
    cursor: pointer;
    height: 24px;
}

.navbar-icon-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.navbar-icon {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.login-prompt {
    background-color: #f7f7f7;
    width: auto;
    padding: 10px 20px;
    position: absolute;
    top: calc(100% + 10px);
    right: 0;
    border: 1px solid #121212;
    border-radius: 14px;
    display: none;
    opacity: 0;
    visibility: hidden;
}

.login-prompt h2 {
    font-size: 16px;
    color: #333;
    margin-bottom: 15px;
}

.login-prompt button {
    display: inline-block;
    width: 100%;
    padding: 10px 15px;
    font-size: 14px;
    color: white;
    background-color: #333;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-prompt button:hover {
    background-color: #0056b3;
}

.login-prompt--open {
    display: block;
    opacity: 1;
    visibility: visible;
}

.navbar__hamburger {
    display: none;
    background: transparent;
    border: transparent;
    color: #121212;
    font-size: 24px;
    align-items: center;
}

@media (max-width: 1000px) {

    .navbar-icons,
    .navbar-categories {
        display: none;
    }

    .navbar__hamburger {
        display: block;
        /* 햄버거 버튼 표시 */
    }
}